import { useEffect } from "react";
import { NavLink } from "react-router-dom";
import { PageTheme } from "./StyleScripts";

export default function Header(props)
{
    useEffect(() =>
    {
        if(localStorage.getItem("Theme"))
        {
            localStorage.getItem("Theme") === "Light" ? 
            document.getElementById("theme-btn").children[0].style.transform = "rotate(0)" :
            document.getElementById("theme-btn").children[0].style.transform = "rotate(180deg)"
        }
    });

    return(
        <header className="light-background-full">
            <NavLink id="home-link" className="home-link" to="/examples/two"><img alt="Logo da empresa." src={"/examples/two/logo.png"}/><span className="logo-txt">Empresa X</span></NavLink>
            <nav className="nav">
                <ul className="nav-link-list">
                    <li><a className="light-text light-backhover" href="#about">Sobre</a></li>
                    <li><a className="light-text light-backhover" href="#services">Servi&ccedil;os</a></li>
                    <li><a className="light-text light-backhover" href="#contact">Contato</a></li>
                </ul>
            </nav>
            <button onClick={(e) => RespNavOpen(e)} className="resp-nav-btn"><img className="no-invert" alt="Ícone de menu." src={"/examples/two/menuIcon.png"}/></button>
            <nav className="resp-nav light-background-full">
                <a onClick={(e) => RespNavOpen(e)} className="light-text light-backhover" href="#about">Sobre</a>
                <a onClick={(e) => RespNavOpen(e)} className="light-text light-backhover" href="#services">Servi&ccedil;os</a>
                <a onClick={(e) => RespNavOpen(e)} className="light-text light-backhover" href="#contact">Contato</a>
            </nav>
            <button tooltip="Altere entre o tema claro e o tema escuro." id="theme-btn" className="change-theme-btn"><img className="no-invert" onClick={(e) => ChangeTheme(e.target)} alt="Ícone do botão de mudar tema." src={"/examples/two/themeIcon.png"}/></button>
        </header>
    );
}

function ChangeTheme(btn)
{
    btn.style.transform = btn.style.transform.includes("180") ? "rotate(0)":"rotate(180deg)";

    if(btn.style.transform.includes("180"))
    {
        localStorage.setItem("Theme", "Dark");
        PageTheme(localStorage.getItem("Theme"));
    }
    else
    {
        localStorage.setItem("Theme", "Light");
        PageTheme(localStorage.getItem("Theme"));
    }
}

function RespNavOpen(e)
{
    if(document.querySelector(".resp-nav").style.display.includes("flex"))
    {
        document.querySelector(".resp-nav").animate([{opacity: 1, top: "102%"},{top: 0, opacity: 0}], {duration: 200})
        .addEventListener("finish", () => document.querySelector(".resp-nav").style.display = "none");
    }
    else
    {
        document.querySelector(".resp-nav").style.display = "flex";
        document.querySelector(".resp-nav").animate([{opacity: 0, top: 0},{top: "102%", opacity: 1}], {duration: 200});
    }

}