export function PageTheme(Theme)
{
  switch(Theme)
  {
    case "Light":
    {
      document.querySelectorAll(".dark-background-full").forEach((e) =>
      {
        e.classList.replace("dark-background-full", "light-background-full");
      });
      document.querySelectorAll(".dark-background").forEach((e) =>
      {
        e.classList.replace("dark-background", "light-background");
      });
      document.querySelectorAll(".light-text").forEach((e) =>
      {
        e.classList.replace("light-text", "dark-text");
      });
      document.querySelectorAll(".invert").forEach((e) =>
      {
        e.classList.replace("invert", "no-invert");
      });
      document.querySelectorAll(".dark-backhover").forEach((e) =>
      {
        e.classList.replace("dark-backhover", "light-backhover");
      });
      document.querySelectorAll(".svg-dark-back").forEach((e) =>
      {
        e.classList.replace("svg-dark-back", "svg-light-back");
      });
      document.querySelectorAll(".svg-dgray-back").forEach((e) =>
      {
        e.classList.replace("svg-dgray-back", "svg-lgray-back");
      });

      break;
    }
    case "Dark":
    {
      document.querySelectorAll(".light-background-full").forEach((e) =>
      {
        e.classList.replace("light-background-full", "dark-background-full");
      });
      document.querySelectorAll(".light-background").forEach((e) =>
      {
        e.classList.replace("light-background", "dark-background");
      });
      document.querySelectorAll(".dark-text").forEach((e) =>
      {
        e.classList.replace("dark-text", "light-text");
      });
      document.querySelectorAll(".no-invert").forEach((e) =>
      {
        e.classList.replace("no-invert", "invert");
      });
      document.querySelectorAll(".light-backhover").forEach((e) =>
      {
        e.classList.replace("light-backhover", "dark-backhover");
      });
      document.querySelectorAll(".svg-light-back").forEach((e) =>
      {
        e.classList.replace("svg-light-back", "svg-dark-back");
      });
      document.querySelectorAll(".svg-lgray-back").forEach((e) =>
      {
        e.classList.replace("svg-lgray-back", "svg-dgray-back");
      });

      break;
    }
    default:
    {
      document.querySelectorAll(".dark-background-full").forEach((e) =>
      {
        e.classList.replace("dark-background-full", "light-background-full");
      });
      document.querySelectorAll(".dark-background").forEach((e) =>
      {
        e.classList.replace("dark-background", "light-background");
      });
      document.querySelectorAll(".light-text").forEach((e) =>
      {
        e.classList.replace("light-text", "dark-text");
      });
      document.querySelectorAll(".invert").forEach((e) =>
      {
        e.classList.replace("invert", "no-invert");
      });
      document.querySelectorAll(".dark-backhover").forEach((e) =>
      {
        e.classList.replace("dark-backhover", "light-backhover");
      });
      document.querySelectorAll(".svg-dark-back").forEach((e) =>
      {
        e.classList.replace("svg-dark-back", "svg-light-back");
      });
      document.querySelectorAll(".svg-dgray-back").forEach((e) =>
      {
        e.classList.replace("svg-dgray-back", "svg-lgray-back");
      });

      break;
    }
  }
}

export function Adjustment()
{
  window.addEventListener("scroll", function()
  {
    if(this.document.scrollingElement.scrollTop >= this.document.documentElement.clientHeight)
    {
      this.document.querySelector(".backtop-btn").style.display = "initial";
    }
    else
    {
      this.document.querySelector(".backtop-btn").style.display = "none";
    }
  });
  window.addEventListener("resize", function()
  {
    this.document.querySelector(".resp-nav").style.display = "none";

    if(this.document.documentElement.clientWidth <= 889)
    {
      this.document.querySelector(".resp-nav-btn").style.display = "initial";
    }
    else if(this.document.documentElement.clientWidth >= 890)
    {
      this.document.querySelector(".resp-nav-btn").style.display = "none";
    }
  });
  window.addEventListener("load", function()
  {
    this.document.querySelector(".resp-nav").style.display = "none";

    if(this.document.documentElement.clientWidth <= 889)
    {
      this.document.querySelector(".resp-nav-btn").style.display = "initial";
    }
    else if(this.document.documentElement.clientWidth >= 890)
    {
      this.document.querySelector(".resp-nav-btn").style.display = "none";
    }
  });
}