import './App.css';
import './About.css';
import './Services.css';
import './Contact.css';
import './Themes.css';
import {useEffect} from "react";
import Header from './Header';
import Footer from './Footer';
import { PageTheme, Adjustment } from './StyleScripts';
import About from './About';
import Banner from './Banner';
import Services from './Services';
import Contact from './Contact';
import BackTopButton from './BackTopButton';

export default function App(props) 
{
  useEffect(() =>
  {
    PageTheme(localStorage.getItem("Theme") || "Light");
    Adjustment();
  });

  return (
    <div className="app light-background-full">
      <Header />
      <Banner />
      <main className="section-container light-background-full">
        <div id="about" className="separator"></div>
        <About />
        <div id="services" className="separator"></div>
        <Services />
        <div id="contact" className="separator"></div>
        <Contact />
      </main>
      <BackTopButton />
      <Footer />
    </div>
  );
}