export default function ContactCard(props)
{
    return(
    <div className="contact-card light-backhover">
        {props.cardtype.toLowerCase() === "link" ? 
        <a className="contact-card-link light-text" href={props.link}><img alt="Ilustração do serviço." src={"/examples/two/"+props.cardimg}/>{props.cardtitle}</a>
        :
        <p className="contact-card-link light-text" href={props.link}><img className={props.cimgClasses || null} alt="Ilustração do serviço." src={"/examples/two/"+props.cardimg}/>{props.cardtitle+":"}
        <span className="contact-card-content">{props.content}</span></p>}
    </div>
    )
}