export default function ServiceCard(props)
{
    return(
    <div className="service-card light-backhover">
        <p className="service-title light-text">{props.cardtitle}</p>
        <img alt="Ilustração do serviço." src={"/examples/two/"+props.cardimg}/>
        <p className="service-desc light-text">{props.carddesc}</p>
    </div>
    )
}