const axios = require("axios");
export async function load_about()
{
    if(sessionStorage.getItem("AboutText"))
    {
        return document.getElementById("about-txtarea").innerText = sessionStorage.getItem("AboutText");
    }
    else
    {
        await axios.get("https://kx-sandbox-api.herokuapp.com/fetch/about", {withCredentials: true, method: "GET"}).then((res) =>
        {
            sessionStorage.setItem("AboutText", res.data.content);
            return document.getElementById("about-txtarea").innerText = res.data.content;
        });
    }
}